import React, { Component } from "react";
import "./App.css";
import Navbar from "./Navigationbar/Navigationbar";
import Banner from "./Banner/Bannner";
import About from "./About/About";
import Projects from "./Projects/Projects";
import EducationAndSkills from "./Skills/EducationAndSkills";
import Story from "./Story/Story";
import Contact from "./Contact/Contact";

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      NavbarElementAppear: false,
      windowHeight: window.innerHeight
    };
  }

  onNameTypeFinished = () => {
    let windowHeight = this.state.windowHeight;
    this.setState({ NavbarElementAppear: true, windowHeight: windowHeight });
  };

  render() {
    return (
      <div className="App">
        <Banner
          typingFinished={this.onNameTypeFinished}
          windowHeight={this.state.windowHeight}
        />
        <Navbar
          NavbarElementAppear={this.state.NavbarElementAppear}
          windowHeight={this.state.windowHeight}
        />
        <About />
        <Projects />
        <EducationAndSkills />
        <Story />
        <Contact />
        <div
          style={{
            backgroundColor: "rgb(33, 33, 33)",
            color: "white"
          }}
        >
          Icons made by different authors from{" "}
          <a href="https://www.flaticon.com/" title="Flaticon">
            www.flaticon.com
          </a>
        </div>
      </div>
    );
  }
}

export default App;
