import React, { Component } from "react";
import { Card, CardGroup, Container } from "react-bootstrap";
// import NoImg from "./Images/NoImg.png";
import DensityFunctionalTheory from "./Images/DensityFunctionalTheory.png";
import Indoorpositioning from "./Images/Indoorpositioning.png";
import LocalizationAndMapping from "./Images/LocalizationAndMapping.png";
import ManyInOneWearable from "./Images/ManyInOneWearable.png";
import PrintedCpwAntenna from "./Images/PrintedCpwAntenna.png";
import PrintedElectronicsFor from "./Images/PrintedElectronicsFor.png";
import PrintedMicrostripPatch from "./Images/PrintedMicrostripPatch.png";
import TransparentOxides from "./Images/TransparentOxides.png";
import "./Projects.css";

class Projects extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    let border = { border: 0 };
    return (
      <Container className="Projects" id="Projects">
        <h1> My Projects </h1>

        <CardGroup className="ProjectCardGroup">
          <Card className="text-black " style={border}>
            <Card.Img
              variant="top"
              src={PrintedCpwAntenna}
              className="ProjectCardImage"
            />
            <Card.Body>
              <Card.Title>
                Printed CPW antenna on PET operating at WiFi and GPS frequencies
              </Card.Title>
              <Card.Text className="ProjectText">
                Designed Co-Planar Waveguide (CPW) fed antennas for both GPS
                (1.575 GHz) and Wi-Fi (2.4 GHz) applications on FR-4 and thin
                PET substrates.
              </Card.Text>
            </Card.Body>
          </Card>
          <Card className="text-black " style={border}>
            <Card.Img
              variant="top"
              src={PrintedElectronicsFor}
              className="ProjectCardImage"
            />
            <Card.Body>
              <Card.Title>
                Printed electronics for wireless in-package food freshness
                monitoring
              </Card.Title>
              <Card.Text className="ProjectText">
                Developed and fabricated multiple printed antenna reader
                congurations and improved the read-out distance of NFC tags by
                nearly 200% without increasing the power usage.
              </Card.Text>
            </Card.Body>
          </Card>
          <Card className="text-black " style={border}>
            <Card.Img
              variant="top"
              src={PrintedMicrostripPatch}
              className="ProjectCardImage"
            />
            <Card.Body>
              <Card.Title>
                Printed microstrip patch antenna operating at Wi-Fi frequency
              </Card.Title>
              <Card.Text className="ProjectText">
                Designed a microstrip circular patch antenna to be printed photo
                paper to operate at 2.4 GHz as a part of an independent
                undergraduate research.
              </Card.Text>
            </Card.Body>
          </Card>
        </CardGroup>
        <CardGroup className="ProjectCardGroup">
          <Card className="text-black " style={border}>
            <Card.Img
              variant="top"
              src={ManyInOneWearable}
              className="ProjectCardImage"
            />
            <Card.Body>
              <Card.Title>
                Many-in-one wearable virtual musical instruments
              </Card.Title>
              <Card.Text className="ProjectText">
                Developing structure-less musical instruments using a single
                pair of sensor-loaded gloves.
              </Card.Text>
            </Card.Body>
          </Card>
          <Card className="text-black " style={border}>
            <Card.Img
              variant="top"
              src={DensityFunctionalTheory}
              className="ProjectCardImage"
            />
            <Card.Body>
              <Card.Title>
                Density Functional Theory calculations of phosphorus doping in
                NiO
              </Card.Title>
              <Card.Text className="ProjectText">
                The generalized gradient approximation (GGA) with empirical
                self-interaction corrections (GGA+U) was applied to correct for
                the overestimation of covalency which is intrinsic to GGA-DFT
                calculations
              </Card.Text>
            </Card.Body>
          </Card>
          <Card className="text-black " style={border}>
            <Card.Img
              variant="top"
              src={LocalizationAndMapping}
              className="ProjectCardImage"
            />
            <Card.Body>
              <Card.Title>Localization and Mapping</Card.Title>
              <Card.Text className="ProjectText">
                Built a robot that localizes itself in a closed room and maps
                the target Wi-Fi node using a trilateration algorithm based on
                Projections onto convex sets, while representing IIT Madras in
                the 5th Inter-IIT Tech Meet.
              </Card.Text>
            </Card.Body>
          </Card>
        </CardGroup>
        <CardGroup className="ProjectCardGroup">
          <Card className="text-black " style={border}>
            <Card.Img
              variant="top"
              src={Indoorpositioning}
              className="ProjectCardImage"
            />
            <Card.Body>
              <Card.Title>Indoor Positioning System using Wi-Fi</Card.Title>
              <Card.Text className="ProjectText">
                Developed an indoor positioning system using WiFi-enabled
                ESP8266-01 IoT modules with an accuracy of 1-2m.
              </Card.Text>
            </Card.Body>
          </Card>
          <Card className="text-black " style={border}>
            <Card.Img
              variant="top"
              src={TransparentOxides}
              className="ProjectCardImage"
            />
            <Card.Body>
              <Card.Title>
                Transition metal oxides for transparent thin films
              </Card.Title>
              <Card.Text className="ProjectText">
                Presented to a class of multidisciplinary undergraduate and
                graduate students about the latest research on transparent
                photovoltaics and thin films
              </Card.Text>
            </Card.Body>
          </Card>
        </CardGroup>
      </Container>
    );
  }
}

export default Projects;
