import React, { Component } from "react";
import { Card, CardGroup } from "react-bootstrap";
import ProfilePic from "./Images/profilePic.jpg";
import "./About.css";

class About extends Component {
  constructor(props) {
    super(props);
    this.state = {
      about:
        "Lorem ipsum dolor sit amet, consectetuer adipiscing elit. Aenean commodo ligula eget dolor. Aenean massa. Cum sociis natoque ",
    };
  }
  render() {
    let style = { border: 0 };
    return (
      <div className="About" id="About">
        <h1>About Me</h1>
        <CardGroup className="AboutCardGroup">
          <Card style={style}>
            <Card.Img src={ProfilePic} className="ProfilePic" />
          </Card>

          <Card style={style}>
            <Card.Body className="AboutText">
              <p>
                Hello, there! I am Sai Chandrahaas Vadali, a first year Master
                of Science student (MS) in Material Science and Engineering at
                Stanford University. I am actively looking for internships for
                the summer of 2020. My primary interests lie in the field of
                semiconductor devices, modeling, and fabrication.
              </p>
              <p>
                I have always been at heart, an engineer first, and then a
                materials engineer. A modern-day engineer should be well
                equipped not just to be able to solve problems but also to able
                to ask tough questions which are necessary to change the
                innovation landscape. True to this philosophy of mine, I have
                explored several areas of engineering in my time as an undergrad
                at IIT Madras to further my spirit of scientific inquiry.
              </p>
              <p>
                Prior to diving into undergraduate research, I spent a fair
                amount of time in the student Electronics Club, where I was
                acquainted with using basic lab tools, soldering, PCB design and
                system level design. This kind of hands-on approach towards
                engineering laid foundation to my subsequent research
                undertaking. As a part of my bachelor’s thesis, I worked on
                flexible printed antennas in a start-up, Is It Fresh GmbH, in
                Germany. The interdisciplinary nature of the project - nuances
                of antenna design and fabrication of thin-film aluminium NFC
                tags- was definitely exciting for me. It was here that I learnt
                to develop, plan and execute engineering projects at consumer
                scale in compliance to industry standards. In addition to
                research work, coursework on semiconductor devices, device
                modelling and manufacturing technologies helped me gain a more
                complete perspective in the field of hardware.
              </p>
              <p>
                On the co-curricular front,I have served as the head for
                multiple student organizations during my junior year at IIT
                Madras which have contributed to my holistic development and
                have made me a well-rounded individual.
              </p>
              <p>
                In my two quarters here at Stanford, I have taken up course to
                explore the breadth of material science and latest technological
                trends – from nanophotonics, organic semiconductors and wide
                band gap materials to quantum computing. The upcoming quarter I
                am planning to take more courses on advanced IC fabrication
                techniques and advanced power semiconductor devices. I am
                currently working in Prof. Alberto Salleo’s lab on polymeric
                ionic gels for neuromorphic computing where I picked up solution
                processing and working in a glove box. Simultaneously, I am also
                working as a graduate student researcher in Dr. Pablo Paredes’
                Pervasive Wellbeing Technology Lab. I am working on
                non-intrusive detection of stress through human-computer
                interactions.
              </p>
            </Card.Body>
          </Card>
        </CardGroup>
      </div>
    );
  }
}

export default About;
