import React, { Component } from "react";
import "./Banner.css";

import Typewriter from "typewriter-effect";
import { CardGroup, Card } from "react-bootstrap";

class Banner extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    let style = { height: 0.8 * this.props.windowHeight };

    return (
      <div className="backgroundImage" style={style}>
        <CardGroup className="BannerContent">
          <Card className="BannerName">
            <Card.Body>
              <Typewriter
                onInit={typewriter => {
                  typewriter
                    .typeString("Hi, there! I'm Chandrahaas.")
                    .callFunction(() => {
                      this.props.typingFinished();
                    })
                    .stop()
                    .start();
                }}
              />
            </Card.Body>
          </Card>
        </CardGroup>
      </div>
    );
  }
}

export default Banner;
