import React, { Component } from "react";
import { Navbar, Nav, Container } from "react-bootstrap";
import "./Navigationbar.css";

class NavigationBar extends Component {
  constructor(props) {
    super(props);
    this.state = { isSticky: false };
    this.ref = React.createRef();
    this.counter = 0;
  }

  handleScroll = () => {
    if (this.ref.current.getBoundingClientRect().top <= 0) {
      this.setState({ isSticky: true });
    } else {
      this.setState({ isSticky: false });
    }
  };

  render() {
    let style = { height: 0.2 * this.props.windowHeight };
    window.addEventListener("scroll", this.handleScroll);
    let display = null;
    let displayDiv = null;

    if (this.props.NavbarElementAppear) {
      if (this.counter < 1) {
        displayDiv = `sticky-wrapper${
          this.state.isSticky
            ? " sticky"
            : " notSticky animated fadeInUpBig slower"
        }`;
        this.counter++;
      } else {
        displayDiv = `sticky-wrapper${
          this.state.isSticky ? " sticky" : " notSticky "
        }`;
      }
      display = (
        <div className={displayDiv}>
          <Container>
            <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark">
              <Navbar.Toggle aria-controls="responsive-navbar-nav" />
              <Navbar.Collapse id="responsive-navbar-nav">
                <Nav className="text-black">
                  <Nav.Link href="#About">About Me</Nav.Link>
                  <Nav.Link href="#Projects">Projects</Nav.Link>
                  <Nav.Link href="#EducationAndSkills">Skills</Nav.Link>
                  <Nav.Link href="#Story">Story</Nav.Link>
                  <Nav.Link href="#Contact">Contact</Nav.Link>
                </Nav>
              </Navbar.Collapse>
            </Navbar>
          </Container>
        </div>
      );
    } else {
      display = null;
    }
    return (
      <div style={style} ref={this.ref}>
        {display}
      </div>
    );
  }
}

export default NavigationBar;
