import React, { Component } from "react";
import { Card, CardDeck, Container, ListGroup } from "react-bootstrap";
// import NoImg from "./Images/NoImg.png";
import Code from "./Images/Code.png";
import SoftwareAndTools from "./Images/SoftwareAndTools.png";
import Hardware from "./Images/Hardware.png";
import "./Skills.css";

class Skills extends Component {
  constructor(props) {
    super(props);
    this.state = {
      SoftwareAndTools: [
        "CST Microwave Studio",
        "Verilog A",
        " Quantum Espresso",
        "OpenFoam",
        "ThermoCalc",
        "Vesta",
        "Micress",
        "Verilog HDL",
        "TI - Code Composer Studio",
        "Octave"
      ],
      Hardware: [
        "Microcontrollers - ATmega",
        "TI-MSP430 series",
        "IoT modules - ESP-01",
        "lab tools"
      ],
      Programming: ["Python", "Java", "C", " C++", "Lua", "MATLAB"]
    };
  }
  render() {
    return (
      <Container className="Skills" id="Skills">
        <h3>Skills</h3>
        <CardDeck className="SkillCardDeck">
          <Card className="text-black ">
            <Card.Img
              variant="top"
              src={SoftwareAndTools}
              className="SkillCardImage"
            />
            <Card.Header>Software and tools</Card.Header>
            <ListGroup variant="flush">
              {this.state.SoftwareAndTools.map((SoftwareAndTool, index) => {
                return (
                  <ListGroup.Item key={index}>{SoftwareAndTool}</ListGroup.Item>
                );
              })}
            </ListGroup>
          </Card>
          <Card className="text-black ">
            <Card.Img variant="top" src={Hardware} className="SkillCardImage" />
            <Card.Header>Hardware</Card.Header>
            <ListGroup variant="flush">
              {this.state.Hardware.map((Hardware, index) => {
                return <ListGroup.Item key={index}>{Hardware}</ListGroup.Item>;
              })}
            </ListGroup>
          </Card>
          <Card className="text-black ">
            <Card.Img variant="top" src={Code} className="SkillCardImage" />
            <Card.Header>Programming Languages</Card.Header>
            <ListGroup variant="flush">
              {this.state.Programming.map((Language, index) => {
                return <ListGroup.Item key={index}>{Language}</ListGroup.Item>;
              })}
            </ListGroup>
          </Card>
        </CardDeck>
      </Container>
    );
  }
}

export default Skills;
