import React, { Component } from "react";
import { Container } from "react-bootstrap";
import "./Contact.css";
import { FaEnvelope } from "react-icons/fa";
import { FaLinkedin } from "react-icons/fa";
import { IconContext } from "react-icons";
class EndBanner extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  MailIconCLicked = () => {
    const el = document.createElement("textarea");
    el.value = "cvadali@stanford.edu";
    document.body.appendChild(el);
    el.select();
    document.execCommand("copy");
    document.body.removeChild(el);
    alert("Email-ID copied to clipboard");
  };

  LinkedinIconClicked = () => {
    window.open("https://www.linkedin.com/in/cvadali", "_blank");
  };

  render() {
    return (
      <Container fluid={true} className="EndBanner" id="Contact">
        <h1>Get in Touch</h1>
        <IconContext.Provider value={{ size: "2em" }}>
          <Container fluid={false} className="ContactBtnContainer">
            <div className="ContactBtn">
              <FaEnvelope
                className="MailContact"
                onClick={this.MailIconCLicked}
              />
            </div>
            <div className="ContactBtn">
              <FaLinkedin
                className="MailContact "
                onClick={this.LinkedinIconClicked}
              />
            </div>
          </Container>
        </IconContext.Provider>
      </Container>
    );
  }
}

export default EndBanner;
